jest.mock('../../../src/screens/Dashboard/components/QuickActions', () => {
  const { View, Text } = require('react-native');
  return ({ currentLevel }) => (
    <View testID="quick-actions">
      <Text>Quick Actions for Level: {currentLevel}</Text>
    </View>
  );
});

jest.mock('../../../src/screens/Dashboard/components/SimpleMetrics', () => {
  const { View, Text } = require('react-native');
  return ({ accentColor }) => (
    <View testID="simple-metrics">
      <Text>Metrics with color: {accentColor}</Text>
    </View>
  );
});

jest.mock('../../../src/screens/Dashboard/components/LearningProgress', () => {
  const { View, Text, TouchableOpacity } = require('react-native');
  return ({ globalProgress, levels, currentLevel, onSelectLevel, onChangeLevelVisual }) => (
    <View testID="learning-progress">
      <Text>Global Progress: {globalProgress}%</Text>
      <Text>Current Level: {currentLevel}</Text>
      {levels.map(level => (
        <TouchableOpacity
          key={level.id}
          onPress={() => onSelectLevel?.(level.id)}
          testID={`level-${level.id}`}
        >
          <Text>Level {level.id}: {level.progress}%</Text>
        </TouchableOpacity>
      ))}
      <TouchableOpacity
        onPress={() => onChangeLevelVisual?.('2')}
        testID="change-level-visual"
      >
        <Text>Change Visual Level</Text>
      </TouchableOpacity>
    </View>
  );
});

