jest.mock('../../../src/components/layout/Container', () => {
  const { View } = require('react-native');
  const Container = ({ children, ...props }) => <View {...props}>{children}</View>;
  Container.CONTAINER_SAFE_EDGES = {
    NO_BOTTOM: ['top', 'left', 'right']
  };
  return Container;
});

// Mock du RevisionOrchestrator
jest.mock('../../../src/screens/VocabularyRevision/RevisionOrchestrator', () => {
  const { View, Text } = require('react-native');
  return ({ currentLevel }) => (
    <View testID="revision-orchestrator">
      <Text>Revision for level: {currentLevel}</Text>
    </View>
  );
});

// Mock des constantes
jest.mock('../../../src/utils/constants', () => ({
  LANGUAGE_LEVELS: {
    '1': { title: 'Débutant', color: '#10B981', icon: '🌱' },
    '2': { title: 'Élémentaire', color: '#3B82F6', icon: '📚' },
    '3': { title: 'Intermédiaire', color: '#8B5CF6', icon: '🎯' },
    '4': { title: 'Intermédiaire+', color: '#F59E0B', icon: '🚀' },
    '5': { title: 'Avancé', color: '#EF4444', icon: '⭐' },
    '6': { title: 'Expert', color: '#EC4899', icon: '👑' },
    'bonus': { title: 'Bonus', color: '#6366F1', icon: '🎁' }
  },
  EXERCISES: {
    vocabulary: { route: '/tabs/vocabulary' },
    grammar: { route: '/tabs/grammar' }
  },
  CONTAINER_SAFE_EDGES: {
    NO_BOTTOM: ['top', 'left', 'right']
  }
}));

const { router } = require('expo-router');

describe('Dashboard', () => {
  const mockThemeContext = {
    colors: {
      background: '#F8FAFC',
      primary: '#3B82F6',
      surface: '#FFFFFF',
      text: '#1F2937',
      textSecondary: '#6B7280'
    }
  };

  const mockProgressContext = {
    progress: { level1: 50, level2: 30 },
    isLoading: false
  };

  const mockCurrentLevelContext = {
    setCurrentLevel: jest.fn()
  };

  beforeEach(() => {
    jest.clearAllMocks();
    useProgress.mockReturnValue(mockProgressContext);
    useCurrentLevel.mockReturnValue(mockCurrentLevelContext);
    router.push.mockClear();
  });

  const renderDashboard = (themeValue = mockThemeContext) => {
    return render(
      <ThemeContext.Provider value={themeValue}>
        <Dashboard />
      </ThemeContext.Provider>
    );
  };

  describe('Rendu initial', () => {
    it('devrait afficher tous les composants principaux', async () => {
      const { getByTestId } = renderDashboard();

      await waitFor(() => {
        expect(getByTestId('modern-header')).toBeTruthy();
        expect(getByTestId('hero-continue-section')).toBeTruthy();
        expect(getByTestId('quick-actions')).toBeTruthy();
        expect(getByTestId('simple-metrics')).toBeTruthy();
        expect(getByTestId('learning-progress')).toBeTruthy();
        expect(getByTestId('revision-orchestrator')).toBeTruthy();
      });
    });

    it('devrait afficher l\'état de chargement quand les données sont en cours de chargement', () => {
      useProgress.mockReturnValue({ ...mockProgressContext, isLoading: true });

      const { getByText } = renderDashboard();

      expect(getByText('Chargement du tableau de bord...')).toBeTruthy();
    });

    it('devrait utiliser le niveau par défaut (1)', async () => {
      const { getByText } = renderDashboard();

      await waitFor(() => {
        expect(getByText('Header Level: 1')).toBeTruthy();
        expect(getByText('Quick Actions for Level: 1')).toBeTruthy();
      });
    });
  });

  describe('Gestion des niveaux', () => {
    it('devrait afficher la couleur du niveau courant', async () => {
      const { getByText } = renderDashboard();

      await waitFor(() => {
        expect(getByText('Color: #10B981')).toBeTruthy(); // Couleur du niveau 1
      });
    });

    it('devrait synchroniser le niveau avec le contexte global lors du changement visuel', async () => {
      const { getByTestId } = renderDashboard();

      await waitFor(() => {
        const changeButton = getByTestId('change-level-visual');
        fireEvent.press(changeButton);
      });

      expect(mockCurrentLevelContext.setCurrentLevel).toHaveBeenCalledWith('2');
    });

  it('devrait naviguer vers l\'exercice approprié avec les bons paramètres', async () => {
    // Mock lastActivity avec type 'vocabulary' et level '1'
    useLastActivity.mockReturnValue({
      lastActivity: {
        type: 'vocabulary',
        level: '1',
        title: 'Vocabulaire Animaux',
        metadata: { word: 5, totalWords: 15 }
      },
      isLoading: false,
      reload: jest.fn().mockResolvedValue()
    });

    const { getByTestId } = renderDashboard();

    await waitFor(() => {
      const continueButton = getByTestId('continue-button');
      fireEvent.press(continueButton);
    });

    expect(router.push).toHaveBeenCalledWith({
      pathname: '/tabs/vocabulary',
      params: { level: '1' }
    });
  });
});


describe('Navigation Continue', () => {
  // Mise à jour du mock useLastActivity selon chaque scénario 
  const useLastActivity = require('../../../src/hooks/useLastActivity').default;

  it('devrait naviguer vers levelSelection quand l\'activité est "levelSelection"', async () => {
    // Mock lastActivity avec type 'levelSelection'
    useLastActivity.mockReturnValue({
      lastActivity: { type: 'levelSelection' },
      isLoading: false,
      reload: jest.fn().mockResolvedValue()
    });

    const { getByTestId } = renderDashboard();

    await waitFor(() => {
      const continueButton = getByTestId('continue-button');
      fireEvent.press(continueButton);
    });

    expect(router.push).toHaveBeenCalledWith('/tabs/levelSelection');
  });

  describe('Progression des niveaux', () => {
    it('devrait afficher la progression globale du niveau courant', async () => {
      const { getByText } = renderDashboard();

      await waitFor(() => {
        expect(getByText('Global Progress: 25%')).toBeTruthy();
      });
    });

    it('devrait afficher tous les niveaux avec leur progression', async () => {
      const { getByText } = renderDashboard();

      await waitFor(() => {
        expect(getByText('Level 1: 25%')).toBeTruthy();
        expect(getByText('Level 2: 50%')).toBeTruthy();
        expect(getByText('Level 3: 75%')).toBeTruthy();
      });
    });
  });

  describe('Pull-to-refresh', () => {
    it('devrait gérer le pull-to-refresh', async () => {
      const { getByTestId } = renderDashboard();

      await waitFor(() => {
        expect(getByTestId('modern-header')).toBeTruthy();
      });
    });
  });

  describe('Thème et couleurs', () => {
    it('devrait utiliser les couleurs du thème', async () => {
      const customTheme = {
        colors: {
          background: '#000000',
          primary: '#FF0000',
          surface: '#111111',
          text: '#FFFFFF',
          textSecondary: '#CCCCCC'
        }
      };

      const { getByTestId } = renderDashboard(customTheme);

      await waitFor(() => {
        expect(getByTestId('modern-header')).toBeTruthy();
        expect(getByTestId('simple-metrics')).toBeTruthy();
      });
    });

    it('devrait fonctionner sans contexte de thème', async () => {
      const { getByTestId } = render(<Dashboard />);

      await waitFor(() => {
        expect(getByTestId('modern-header')).toBeTruthy();
        expect(getByTestId('hero-continue-section')).toBeTruthy();
      });
    });
  });

  describe('Gestion des erreurs', () => {
    it('devrait gérer les erreurs de contexte Progress', () => {
      useProgress.mockReturnValue({ isLoading: true });

      const { getByText } = renderDashboard();

      expect(getByText('Chargement du tableau de bord...')).toBeTruthy();
    });

    it('devrait gérer les erreurs de contexte CurrentLevel', async () => {
      useCurrentLevel.mockReturnValue({ setCurrentLevel: jest.fn() });

      const { getByTestId } = renderDashboard();

      await waitFor(() => {
        expect(getByTestId('modern-header')).toBeTruthy();
      });
    });
  });

  describe('Intégration des composants', () => {
    it('devrait passer les bonnes props aux composants enfants', async () => {
      const { getByText } = renderDashboard();

      await waitFor(() => {
        expect(getByText('Header Level: 1')).toBeTruthy();
        expect(getByText('Quick Actions for Level: 1')).toBeTruthy();
        expect(getByText('Metrics with color: #10B981')).toBeTruthy();
        expect(getByText('Current Level: 1')).toBeTruthy();
        expect(getByText('Revision for level: 1')).toBeTruthy();
      });
    });

    it('devrait mettre à jour les composants quand le niveau change', async () => {
      const { getByText } = renderDashboard();

      await waitFor(() => {
        expect(getByText('Header Level: 1')).toBeTruthy();
        expect(getByText('Current Level: 1')).toBeTruthy();
      });
    });
  });

  describe('Accessibilité', () => {
    it('devrait avoir les testIDs appropriés pour les tests', async () => {
      const { getByTestId } = renderDashboard();

      await waitFor(() => {
        expect(getByTestId('modern-header')).toBeTruthy();
        expect(getByTestId('hero-continue-section')).toBeTruthy();
        expect(getByTestId('quick-actions')).toBeTruthy();
        expect(getByTestId('simple-metrics')).toBeTruthy();
        expect(getByTestId('learning-progress')).toBeTruthy();
        expect(getByTestId('revision-orchestrator')).toBeTruthy();
      });
    });
  });
  });
});